import React from 'react';
import '../assets/css/projects.scss';

const ProjectsPage = () => {
  const projectsList = [
    {
      name: 'Mercury Finance',
      imgSrc: './images/mercury.jpg',
      desc: `An open source website for artificial stocks. This site is built
    using React and combines a variety of libraries. The prices of
    inventories vary dynamically. There is a huge list of inventories
    to purchase. CS50 Finance inspired.`,
      link: 'https://ad-dan.github.io/finance/'
    },
    {
      name: 'Game of Life',
      imgSrc: './images/golife.jpg',
      desc: `The Game of Life, also known as Life, is a cellular automaton designed by John Horton Conway, a British mathematician in 1970. This game has been simulated using React.`,
      link: 'https://codepen.io/adunreal/full/BmZbEO/'
    },
    {
      name: '2048',
      imgSrc: './images/2048.jpg',
      desc: `Game 2048 's responsive, mobile friendly clone. Driven by React and HammerJS`,
      link: 'https://ad-dan.github.io/two-four/'
    },
    {
      name: 'Todoer - Mobile',
      imgSrc: './images/todoer.jpg',
      desc: `It's a cross- platform application that works on both iOS and Android. Inspired by the visual design of android OS`,
      link: 'https://github.com/ad-dan/rn-td'
    },
    {
      name: 'Artworks',
      imgSrc: './images/jaime.jpg',
      desc: 'Artworks maded using Adobe Illustrator. Minimalistic Vector art ',
      link: 'https://twitter.com/AdDenticle'
    }
  ];
  const projects = projectsList.map(proj => {
    return (
      <div className="project-box">
        <div className="img-cont">
          <img src={proj.imgSrc} />

          <a href={proj.link} target="_blank">
            <div className="tp">
              <span>{proj.name}</span>
            </div>
          </a>
        </div>
        <div className="desc">
          <span>{proj.desc}</span>
        </div>
      </div>
    );
  });
  return (
    <div>
      <div className="container">
        <div className="title">My Projects</div>

        {projects}
      </div>
    </div>
  );
};

export default ProjectsPage;
